import React from "react"
import Helmet from "react-helmet"
import { graphql } from "gatsby"
import Particles from 'react-particles-js'
import OnVisible, { setDefaultProps } from 'react-on-visible'
import Head from "../components/head"
import Intro from "../components/intro"
import AboutMe from "../components/aboutMe"
import CaseStudy from "../components/caseStudy"
import CaseStudyGrid from "../components/caseStudyGrid"

setDefaultProps({
    visibleClassName: 'in-viewport',
    percent: 40
});

export default ({ data }) => (
    <main role="main" id="top">
        
        <Head />

        <Helmet
            bodyAttributes={{
                class: "homepage",
            }}
            htmlAttributes = {
                {
                    lang: "en"
                }
            }
        />

        <Particles
            width="100%"
            height="100%"
            className="particles"
            params={{
                "particles": {
                    "number": {
                        "value": 240,
                        "density": {
                            "enable": true,
                            "value_area": 700
                        }
                    },
                    "line_linked": {
                        "enable": true,
                        "opacity": 0.02
                    },
                    "move": {
                        "direction": "right",
                        "speed": 0.1
                    },
                    "size": {
                        "value": 2
                    },
                    "opacity": {
                        "anim": {
                            "enable": true,
                            "speed": 1.5,
                            "opacity_min": 0.05
                        }
                    }
                },
                "retina_detect": true
            }}
        />

        <Intro imgSrc={data.bgImg.childImageSharp.fluid} />

        <AboutMe />

        <section className="section-title" id="projects">
            <h3>Projects I've worked on</h3>
        </section>

        <CaseStudy imgSrc={data.herminius.childImageSharp.fluid} />

        <section className="masonry-layout">
            <CaseStudyGrid
                title="N8 Research"
                description="A WordPress site for the collaboration of the eight most research intensive Universities in the North of England."
                imgSrc={data.microscopes.childImageSharp.fluid}
                imageAlt="Alt"
                linkText="Read More"
                internalUrl="/n8research"
                externalUrl=""
                hoverState="box-container"
            />
            <CaseStudyGrid
                title="Responsive Email Template"
                description=""
                imgSrc={data.emailTemplate.childImageSharp.fluid}
                imageAlt="Alt"
                linkText="Github"
                internalUrl=""
                externalUrl="https://github.com/jonbrentnall/responsive-email-template"
                hoverState="box-overlay"
            />
            <CaseStudyGrid
                title="Jodrell Bank"
                description="Showcasing The Jodrell Bank Observatory."
                imgSrc={data.telescope.childImageSharp.fluid}
                imageAlt="The Lovell Telescope"
                linkText="Read More"
                internalUrl="/jodrell-bank"
                externalUrl=""
                hoverState="reveal-bottom-right"
            />

            <OnVisible className={'masonry-layout__last-panel'}>
                <p>
                    More to come...
                </p>
            </OnVisible>
        </section>
    </main>
)

export const fluidImage = graphql`
    fragment fluidImage on File {
        childImageSharp {
            fluid(quality: 100, maxWidth: 2560) {
                ...GatsbyImageSharpFluid_withWebp
            }
        }
    }
`;

export const pageQuery = graphql`
    query {
        bgImg: file(relativePath: { eq: "bg.jpg" }) {
            ...fluidImage
        }
        herminius: file(relativePath: { eq: "herminius.jpg" }) {
            ...fluidImage
        }
        microscopes: file(relativePath: { eq: "microscopes.jpg" }) {
            ...fluidImage
        }
        telescope: file(relativePath: { eq: "telescope.jpg" }) {
            ...fluidImage
        }
        emailTemplate: file(relativePath: { eq: "email-template.jpg" }) {
            ...fluidImage
        }
    }
`;