import React from "react";
import { Link } from "gatsby"
import Img from "gatsby-image"
import OnVisible, { setDefaultProps } from 'react-on-visible';

setDefaultProps({
    visibleClassName: 'in-viewport',
    percent: 40
});

const CaseStudyGrid = (props) => {
    const internalUrl = props.internalUrl;
    const externalUrl = props.externalUrl;

    return (
        <OnVisible className={`panel ${props.hoverState}`}>

            <Img fluid={props.imgSrc} />

            {props.hoverState === "box-overlay" &&
                <div>
                    <div className="box-top-bottom"></div>
                    <div className="box-left-right"></div>
                </div>
            }

            <div className="content">
                <h3>{props.title}</h3>
                
                {props.description.length > 0 &&
                    <p>
                        {props.description}
                    </p>
                }

                {internalUrl.length > 0 &&
                    <button><Link to={props.internalUrl}>{props.linkText}</Link></button>
                }

                {externalUrl.length > 0 &&
                    <button><a href={props.externalUrl} target="_blank" rel="noopener noreferrer">{props.linkText}</a></button>
                }
            </div>

        </OnVisible>
    )
}

export default CaseStudyGrid